/*  */
/*  */
/*  */
/* Deprectate hc-primary and rename to hc-button */
/* This allows hc-button to be base of all buttons */
/* could add class="hc-button hc-button-error" to apply variant styling */
/*  */
/*  */
/*  */

/* Primary button */
.hc-primary,
.hc-primary:hover,
.hc-primary:active,
.hc-primary:disabled {
  font-family: var(--font-family), "Segoe UI", Tahoma, Geneva, Verdana,
    sans-serif;
  font-size: var(--font-body-size-regular), 1rem;
  font-weight: var(--button-text-weight-regular), 400;
  line-height: var(--button-text-lineheight-small), 1;
  letter-spacing: 0em;
  text-align: left;
  text-align: center;
  margin: auto;
  color: var(--text-on-color);
  border-radius: 2px;
  border: 2px solid transparent;
  min-height: 40px;
  color: var(--text-on-color);
  min-width: 100%;
}

@media (min-width: 40.0625em) {
  .hc-primary,
  .hc-primary:hover,
  .hc-primary:active,
  .hc-primary:disabled {
    min-width: auto;
  }
}

/* Focus styles */
.hc-primary:focus,
.hc-secondary:focus,
.hc-danger:focus {
  border: 2px solid white;
  outline: 2px solid #113c66;
}

/* Background colors */
.hc-primary {
  background-color: var(--button-primary);
}

.hc-primary:hover {
  background-color: var(--button-primary-hover);
}

.hc-primary:active {
  background-color: var(--button-primary-active);
}

.hc-primary:disabled {
  background-color: var(--button-disabled);
  color: var(--text-disabled);
}

/* Secondary button */
.hc-secondary {
  min-height: 40px;
  min-width: 75px;
  font: 400 16px "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  line-height: 24px;
  letter-spacing: 0;
  text-align: left;
  background-color: white;
  color: #113c66;
  margin: auto;
  text-align: center;
  border-radius: 2px;
  border: 2px solid #113c66;
}

/* Background colors */
.hc-secondary:hover {
  color: white;
  border: 2px solid #002852;
  background-color: #002852;
}

.hc-secondary:active {
  background-color: #0a1e48;
}

.hc-secondary:focus {
  min-height: 40px;
  min-width: 80px;
  background-color: #002852;
  color: white;
}

.hc-secondary:disabled {
  color: #c3c6c9;
  background-color: white;
  margin: auto;
  text-align: center;
  border-radius: 2px;
  outline: auto;
  border: 2px solid #c3c6c9;
}

/* Danger button */
.hc-danger {
  min-height: 40px;
  min-width: 75px;
  font: 400 16px "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  line-height: 24px;
  letter-spacing: 0em;
  text-align: left;
  background-color: #ce1432;
  color: white;
  margin: auto;
  text-align: center;
  border-radius: 2px;
  border: 2px solid transparent;
}

.hc-danger:hover {
  background-color: #ba001e;
}

.hc-danger:active {
  background-color: #a6000a;
}

.hc-danger:disabled {
  background-color: #c3c6c9;
  color: #818283;
}

/*  */
/*  */
/* Icons */
/*  */
/*  */
/*  */

.hc-closeIconBlue {
  background-image: url(material-symbols_close-rounded-blue.svg);
  height: 12px;
  width: 12px;
  background-size: cover;
  border: none;
  outline-offset: -2px;
}

.hc-closeIconWhite {
  background-image: url(material-symbols_close-rounded-white.svg);
  height: 12px;
  width: 12px;
  background-size: cover;
  border: none;
  outline-offset: -2px;
}

.hc-closeIconGrey {
  background-image: url(material-symbols_close-rounded.svg);
  height: 12px;
  width: 12px;
  background-size: cover;
  border: none;
  outline-offset: -2px;
}

.hc-secondary:hover .hc-closeIconBlue {
  background-image: url(material-symbols_close-rounded-white.svg);
  height: 12px;
  width: 12px;
}

.hc-primary:disabled .hc-closeIconWhite,
.hc-secondary:disabled .hc-closeIconBlue,
.hc-danger:disabled .hc-closeIconWhite {
  background-image: url(material-symbols_close-rounded.svg);
  height: 12px;
  width: 12px;
  background-size: cover;
  border: none;
  outline-offset: -2px;
}

.hc-danger.hc-closeIconWhite {
  background-image: url(material-symbols_close-rounded-white.svg);
  height: 12px;
  width: 12px;
}
